@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Oleo+Script|Signika|Nunito:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-family: Oleo Script;
  font-weight: 400; }

h2 {
  font-family: Nunito, display;
  font-weight: 500; }

p {
  font-family: Nunito, display;
  margin-top: 1rem;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 2rem; }

.Title-container {
  max-width: 1000px;
  margin: auto;
  text-align: center; }

.Title {
  height: 370px;
  min-height: 300px;
  max-width: 1000px;
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  flex-direction: column; }

.Title-name h1 {
  font-size: 72px;
  color: #2A363B;
  margin: auto; }

.Title-name h2 {
  font-size: 1.7rem;
  color: #2A363B; }

.Title-about {
  margin: auto;
  color: #2A363B;
  max-width: 700px;
  margin-top: 3rem;
  font-family: Nunito, display;
  font-size: 1.7rem;
  font-size: 1.25rem; }

.Home {
  background-color: #2A363B;
  border: 0.5rem solid #2A363B;
  border-top: none; }

.Home-container {
  background-color: #FAFAFA; }

.Navbar {
  background-color: #FAFAFA;
  border: 0.5rem solid #2A363B;
  border-bottom: none; }

.Navbar-container {
  max-width: 1000px;
  margin: auto;
  display: flex;
  flex-direction: row;
  padding: 2.5rem; }

.Navbar-avatar-container {
  width: 75px;
  height: 75px;
  overflow: hidden;
  margin-right: auto;
  border-radius: 3px; }
  .Navbar-avatar-container img {
    max-height: 100px; }

.Navbar-link-container {
  margin-top: auto; }
  .Navbar-link-container a {
    text-decoration: none;
    color: #2A363B;
    font-family: Nunito, display;
    font-weight: 500;
    font-size: 1.2rem;
    margin-left: 1rem; }

.ContentSection-container {
  padding: 2.5rem;
  min-height: 500px;
  display: flex;
  vertical-align: middle; }

.navy {
  background-color: #2A363B;
  color: #FAFAFA; }

.white {
  background-color: #FAFAFA;
  color: #2A363B; }

.ContentSection {
  max-width: 1000px;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: center; }
  .ContentSection h1 {
    font-size: 2.5rem; }
  .ContentSection h2 {
    font-family: Nunito, display;
    font-size: 1.7rem;
    font-weight: 400;
    margin-top: .15rem;
    margin-bottom: .5rem; }
  .ContentSection p {
    font-weight: 400; }

.ContentSection-left {
  text-align: right;
  margin-right: 3.75rem;
  margin-left: auto; }

.ContentSection-image {
  margin-top: 1rem;
  margin-bottom: 1rem;
  overflow: hidden;
  border-radius: 3px;
  max-width: 200px;
  width: 200px;
  max-height: 125px; }
  .ContentSection-image img {
    width: 300px;
    flex-shrink: none; }

.ContentSection-right {
  flex-grow: auto;
  max-width: 500px; }

.ContentDivider {
  position: relative;
  margin: auto;
  border: none; }

.ContentDivider > * {
  width: 75px; }

.ContentDivider-top-white {
  margin: auto;
  position: relative;
  top: 25px;
  right: 2px;
  box-sizing: border-box;
  border-top: 20px #2A363B solid;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.ContentDivider-bottom-white {
  position: relative;
  top: 15px;
  right: 20px;
  margin: auto;
  box-sizing: border-box;
  border-bottom: 20px #FAFAFA solid;
  /* border-right: 15px #FAFAFA solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.ContentDivider-top-white {
  margin: auto;
  position: relative;
  top: 25px;
  right: 2px;
  box-sizing: border-box;
  border-top: 20px #2A363B solid;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.ContentDivider-bottom-white {
  position: relative;
  top: 15px;
  right: 20px;
  margin: auto;
  box-sizing: border-box;
  border-bottom: 20px #FAFAFA solid;
  /* border-right: 15px #FAFAFA solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.ContentDivider-top-navy {
  margin: auto;
  position: relative;
  top: -20px;
  right: 7px;
  box-sizing: border-box;
  border-top: 20px #2A363B solid;
  border-right: 20px #2A363B solid;
  /* border-left: 15px #2A363B solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.ContentDivider-bottom-navy {
  position: relative;
  top: -25px;
  right: 20px;
  margin: auto;
  box-sizing: border-box;
  border-bottom: 20px #FAFAFA solid;
  /* border-right: 15px #FAFAFA solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

.SimpleContentSection-container {
  padding: 2.5rem; }

.SimpleContentSection {
  max-width: 666.66667px;
  margin: auto;
  text-align: center;
  color: #2A363B;
  padding: 4rem 0;
  padding-bottom: 5rem; }

* {
  margin: 0;
  padding: 0; }

